@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1340px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 60px;
  }
}

.page {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  color: #000000;
  background-color: #ffffff;
}

.main-header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-right: auto;
  padding: 10px 20px 20px;
  padding-bottom: 13px;
  border-bottom: 1px solid #e7e7e7;
}
.main-header .no-js {
  padding: 0;
  padding-top: 10px;
}
@media (min-width: 768px) {
  .main-header .no-js {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .main-header {
    align-items: flex-end;
    width: calc(100% - 30px);
    margin: 0 auto;
    padding: 7px 0 30px;
    border: none;
  }
}
@media (min-width: 1024px) {
  .main-header {
    max-width: 1220px;
    padding: 55px 0 48px;
  }
}

.main-header__logo {
  display: flex;
}
.no-js .main-header__logo {
  padding: 0 0 10px 20px;
}
@media (min-width: 768px) {
  .no-js .main-header__logo {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .main-header__logo {
    min-width: 174px;
    margin-right: 20px;
  }
}
@media (min-width: 1024px) {
  .main-header__logo {
    width: 202px;
  }
}

@media (min-width: 768px) {
  .main-header__logo-image {
    width: 174px;
    height: auto;
    margin: 15px 0 0 15px;
  }
}
@media (min-width: 1024px) {
  .main-header__logo-image {
    width: 202px;
    height: auto;
    margin: 0;
  }
}

.main-header__logo:not(.main-header__logo--current):hover {
  opacity: 0.8;
}
.main-header__logo:not(.main-header__logo--current):active {
  opacity: 0.6;
}

.no-js .main-nav {
  width: 100%;
}
@media (min-width: 1024px) {
  .main-nav {
    display: flex;
    max-width: 50%;
    margin-bottom: 8px;
  }
}

.main-nav__toggle {
  position: absolute;
  right: 0;
  width: 52px;
  height: 44px;
  margin-right: 5px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 14px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav__toggle::before {
  top: 14px;
  box-shadow: 0 7px 0 0 #000000;
}
.main-nav__toggle::after {
  bottom: 14px;
}
.no-js .main-nav__toggle {
  display: none;
}
.is-open .main-nav__toggle::before {
  top: 14px;
  box-shadow: none;
  transform: translateY(7px) rotate(-45deg);
}
.is-open .main-nav__toggle::after {
  bottom: 14px;
  transform: translateY(-7px) rotate(45deg);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  position: absolute;
  right: 0;
  left: 0;
  z-index: 3;
  display: none;
  margin: 0;
  margin-top: 53px;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  border-bottom: 1px solid #e7e7e7;
}
.no-js .main-nav__list {
  position: static;
  display: block;
  width: 100%;
  margin-top: 0;
}
@media (min-width: 768px) {
  .no-js .main-nav__list {
    width: 500px;
  }
}
.is-open .main-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    margin-top: 0;
    background-color: transparent;
    border: none;
  }
}
@media (min-width: 1024px) {
  .main-nav__list {
    max-width: 100%;
    padding: 0;
  }
}

.main-nav__item {
  width: 100%;
  padding-top: 22px;
  padding-bottom: 24px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  border-top: 1px solid #e7e7e7;
}
@media (min-width: 1024px) {
  .main-nav__item:last-of-type .main-nav__link {
    margin-right: -18px;
    white-space: pre;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    width: auto;
    margin-right: auto;
    margin-bottom: 10px;
    padding: 0;
    border: none;
  }
}

.main-nav__item:not(.main-nav__item--current):hover {
  opacity: 0.6;
}
.main-nav__item:not(.main-nav__item--current):active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    right: 18px;
    bottom: -8px;
    left: 18px;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 768px) {
  .main-nav__item--current {
    position: relative;
  }
}

.main-nav__item--current-form {
  position: relative;
}
@media (min-width: 1024px) {
  .main-nav__item--current-form::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 18px;
    height: 2px;
    background-color: #68b738;
  }
}

.main-nav__link {
  padding: 22px 35px 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 15px 18px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
  }
}
@media (min-width: 1024px) {
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: 1024px) {
  .main-nav--index .main-nav__link {
    color: #ffffff;
  }
}
@media (min-width: 1024px) {
  .main-nav--index .main-nav__item--current::after {
    background-color: #ffffff;
  }
}

